@media screen and (min-width: 1440px) {
    .balance-card {
     height: 400px;
     width: 500px;
     /* text-align: center; */
     font-size: 14px;
    }
  }


  @media screen and (min-width: 1024px) {
    .balance-card {
     height: 420px;
     width: 470px;
     /* text-align: center; */
     display: flex;
     font-size: 14px;
    }
  }

  @media screen and (min-width: 768px) {
    .balance-card {
     height: 380px;
     /* text-align: center; */
     font-size: 14px;
    }
  }


  @media screen and (min-width: 768px) {
    .chart-container{
     height: 300px;
     width: 450px;
     /* text-align: center; */
     font-size: 14px;
    }
  }

/* Table Css */

@media (max-width: 768px) {
  .grid-container {
    width: 600px;
  }
}


@media (max-width: 425px) {
  .grid-container {
    width: 500px;
  }
}



/* Media query for smaller screens */

.grid-container {
  width: 100%;
}

@media (max-width: 1440px) {
  .grid-container {
    max-width: 800px 
  }
}

@media (max-width: 1066px) {
  .grid-container {
    max-width: 600px 
  }
}

@media (max-width: 768px) {
  .grid-container {
    max-width: 400px 
  }
}

@media (max-width: 600px) {
  .grid-container {
    max-width: 300px 
  }
}


@media (max-width: 425px) {
  .grid-container {
    max-width: 335px; 
  }
}


@media (max-width: 320px) {
  .grid-container {
    max-width: 280px; 
  }
}




  
  
  
  